
#page-blog .titlePage {
  margin-top: 60px;
  background: white;
  padding: 40px 0;
}
.titlePage h2 {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 32px;
  padding-left: 44px;
}
#blog-container {
  padding: 44px 44px;
  background: linear-gradient(#5841B8, #8570DF)
}
#article-img {
  min-height: 500px;
  border-radius: 8px 8px 0 0;
}
#article-by {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 300;
  margin: 0;
  padding-top: 22px;
}
#article-infos {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
#article-infos p {
  margin: 0;
  padding: 22px 0;
}
.article-container {
  margin-bottom: 22px;
}
#article-content {
  margin: 0;
  border-radius: 0 0 8px 8px;
  background: white;
}
#article-content h5 {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}
#article-text {
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 18px;
  margin: 0;
  border-bottom: 1px solid grey;
  padding-bottom: 12px;
}
#read-more p {
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 18px;
  margin: 0;
  padding-top: 10px;
}
#more_btn2 {
  display: block;
  padding: 12px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.6);
  border-radius: 8px;
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  font-size: 22px;
  color: white;
  transition: .5s;
  cursor: pointer;
}
#more_btn2:hover {
  background: rgba(255,255,255,0.8);
  color: black;
  border: 1px solid transparent;
}
@media all and (max-width: 768px) {
#article-infos {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#article-infos.col.m6, #read-more.col.m6 {
    width: 100%;
}
#blog-container {
    padding: 44px 12px;
}
.titlePage h2 {
    padding: 0 22px;
    text-align: center;
}
#read-more {
    text-align: center;
}
}



