
#i-am p {
  font-size: 32px;
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  text-align: center;
  padding: 60px 0;
}
.typed-cursor{
  opacity: 1;
  -webkit-animation: blink 0.7s infinite;
  animation: blink 0.7s infinite;
}
@keyframes blink{
0% { opacity:1;
}
50% { opacity:0;
}
100% { opacity:1;
}
}
@-webkit-keyframes blink{
0% { opacity:1;
}
50% { opacity:0;
}
100% { opacity:1;
}
}



