
#jp-aboutme {
  background: #EBEBEB;
}
#jp-aboutme p {
  text-align: center;
  padding: 80px 80px;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
}
@media all and (max-width: 768px) {
#jp-aboutme p {
    padding-left: 22px;
    padding-right: 22px;
}
}



