
nav {
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border: none;
  height: auto;
  box-shadow: none;
  transition: .5s;
  z-index: 1;
}
nav h1 {
  color: white;
  font-weight: 300;
  font-size: 30px;
  margin: 0;
  width: 100%;
  margin-top: 62px;
  font-family: 'Raleway', sans-serif;
  transition: .5s;
}
#navigation-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-top: 53px;
  transition: .5s;
}
#button-ham {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding-top: 40px;
}
#navigation-link p {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  font-weight: 300;
  transition: .5s;
}
.side-nav {
  background: rgba(55,60,91,0.9);
}
.side-nav li {
  text-align: center;
}
.side-nav li a {
  color: white !important;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 22px;
  margin: 0;
  display: block;
  padding: 22px 0;
  height: inherit !important;
}
nav.shrink {
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  transition: .5s;
}
nav.shrink h1 {
  color: black;
  font-size: 24px;
  margin-top: 17px;
  transition: .5s;
}
nav.shrink #navigation-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-top: 0;
  transition: .5s;
}
nav.shrink #navigation-link p {
  color: black;
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  font-weight: 300;
  transition: .5s;
}
nav.shrink #navigation-link p a {
  color: black;
  transition: .5s;
}
nav.shrink #button-ham {
  padding-top: 0px;
}
nav.shrink #button-ham i {
  color: black;
}



