
#jp-header {
  width: 100%;
  min-height: 100vh;
  background: url('/static/img/background-jp.jpg') no-repeat top center fixed / cover;
}
#header-content {
  width: 100%;
  min-height: 100vh;
  background: rgba(55,60,91,0.4);
}
#header-content h2 {
  text-align: center;
  color: white;
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
}
.row {
  margin-bottom: 0;
}



