
.titlePage {
  margin-top: 60px;
  background: white;
  padding: 40px 0;
}
.titlePage h2 {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 32px;
  padding-left: 44px;
}
#page-portfolio {
  background: linear-gradient(#5841B8, #8570DF)
}
#tags {
  padding-left: 44px;
  padding-top: 80px;
}
.tag-active {
  border: 2px solid white;
  border-radius: 12px;
}
#tags .col {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#tags p {
  color: white;
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
  transition: .5s;
}
#tags p:hover {
  color: #F16F4F;
}
#portfolio-container {
  padding: 80px 44px;
}
#more_btn {
  display: block;
  padding: 12px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.6);
  border-radius: 8px;
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  font-size: 22px;
  color: white;
  transition: .5s;
  cursor: pointer;
}
#more_btn:hover {
  background: rgba(255,255,255,0.8);
  color: black;
  border: 1px solid transparent;
}
@media all and (max-width: 768px) {
#titlePage h2 {
    padding: 0 22px;
    text-align: center;
}
}



