
#jp-button {
  display: block;
  padding: 12px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.6);
  border-radius: 8px;
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  font-size: 22px;
  color: white;
  transition: .5s;
}
#jp-button:hover {
  background: rgba(255,255,255,0.8);
  color: black;
  border: 1px solid transparent;
}



