
#jp-card {
  height: 300px;
}
#jp-card h5 {
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: 300;
  color: white;
  text-align: center;
  width: 100%;
  height: 100%;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
  background: rgba(55,60,91,0.6);
  opacity: 0;
  transition: .5s;
}
#jp-card h5:hover {
  opacity: 1;
}
#jp-card.col {
  padding: 0 !important;
}



