
#projects {
  background: linear-gradient(#5841B8, #8570DF);
}
#projects h4 {
  color: white;
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  text-align: center;
  margin: 0;
  padding: 80px 0;
}
#portfolio {
  padding: 0 80px 80px 80px;
}
#more-projects {
  text-align: center;
  padding-bottom: 80px;
}
#blog {
  background: #F16F4F;
  padding: 80px 0;
}
#blog p {
  color: white;
  font-family: 'Raleway', sans-serif;
  font-size: 22px;
  text-align: center;
}
@media all and (max-width: 768px) {
#portfolio {
    padding: 0 22px 80px 22px;
}
}



