
#jp-contact {
  min-height: 100vh;
  height: auto;
  background: rgba(55,60,91,0.6)
}
#jp-contact h4 {
  color: white;
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  text-align: center;
  margin: 0;
  padding: 80px 0;
}
.input-field {
  font-family: 'Raleway', sans-serif;
  font-size: 22px;
}

/* label color */
.input-field label, .input-field input, .input-field textarea {
  color: white;
}
 
/* label focus color */
.input-field input[type=text]:focus + label, textarea.materialize-textarea:focus:not([readonly]) + label {
  color: #51A7D4;
}
 
/* label underline focus color */
.input-field input[type=text]:focus, textarea.materialize-textarea:focus:not([readonly]) {
  border-bottom: 1px solid #51A7D4;
  box-shadow: 0 1px 0 0 #51A7D4;
}
#contact-captchat {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#contact_submit {
  display: block;
  padding: 12px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.6);
  border-radius: 8px;
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  font-size: 22px;
  color: white;
  transition: .5s;
}
#contact_submit:hover {
  background: rgba(255,255,255,0.8);
  color: black;
  border: 1px solid transparent;
}



